</style> */
<template>
    <div style="display: flex;">
 
        <v-btn fab small :color="active? 'red':'green'" dark 
            v-on:click="active=!active" 
            v-if="$store.state.activeRadio.url!=null && !$store.state.activeRadio.error">
              <v-icon v-if="!active">play_arrow</v-icon>
              <v-icon v-if="active">stop</v-icon>
        </v-btn>

        <v-tooltip left v-if="$store.state.activeRadio.error">
            <template v-slot:activator="{ on }">
                <v-icon color="red" v-on="on">
                    block
                </v-icon>
            </template>
            <span>The Stream is temporarily not available</span>
        </v-tooltip>
        <br>
        <v-card outlined tiled class="mx-auto" max-width="200px" v-on:mouseenter="showVolume = true" v-on:mouseleave="showVolume = false" @click.native="setVolume()" style="background-color:transparent;border:none;display:flex">
					<svg width="21px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
						<path fill="currentColor" d="M19,13.805C19,14.462,18.462,15,17.805,15H1.533c-0.88,0-0.982-0.371-0.229-0.822l16.323-9.055C18.382,4.67,19,5.019,19,5.9V13.805z"/>
					</svg>
					<input v-model.lazy.number="volume" v-show="showVolume" type="range" min="0" max="100"/>
          <div>{{ volume }}%</div>
				</v-card>

      

    </div>
</template>

<script>
  export default {
    data: () => ({      
        show: false,
        volume: 35,
        showVolume: false,
    }),

    computed: {
        active: {
            // getter
            get: function () {
                return this.$store.state.activeRadio.active
            },
            // setter
            set: function (newValue) {
                var activeRadio = this.$store.state.activeRadio;
                activeRadio.active = newValue

                if (newValue){
                    this.$store.state.musicPlayer.play(this.$store.state.activeRadio.url)
                } else {
                    this.$store.state.musicPlayer.stop();
                    this.$store.commit('setActiveRadio', activeRadio );
                }

            }
        }
    },

    methods: {
      setVolume: function() {
        var player = this.$store.state.musicPlayer;
        player.volume(this.volume).then(playResult => {
            console.log("SUCESS");
        }, error => {
            console.log(error)
        }).catch(e => {
            console.log("CATCH: " + e);
        });
      }
    }
  
    
  }
</script>